import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";


const styleObj = {
  border: '1px solid red',
  margin: '10px',
};

function App() {
  return (
  
    <div className="App">
    <Router>
      <h1>Welcome React Router</h1>
      <ul>
        <li><Link to="/">home</Link></li>
        <li><Link to="/a">A</Link></li>
        <li><Link to="/b">B</Link></li>
      </ul>
      
      <Route exact path='/'>Hello</Route>
      <Route path='/a'><A/></Route>
      <Route path='/b'><B/></Route>
    </Router>

    </div>
  );
}

const A = () => {
  return <div style={styleObj}>A</div>
};

function B() {
  return <div style={styleObj}>B</div>
}

export default App;